.results {

  .content {
    margin-left: 0;
  }

  .results__map {
    @extend .show-for-medium-up;
  }

  @media #{$medium-up} {
    @include grid-row(
      $behavior: nest-collapse
    );

    .results__map {
      @include grid-column(
        $columns: 6,
        $collapse: true
      );
      padding-right: 5%;
      min-height: 1px;
      .gmaps {
        height: 100%;
      }
    }

    .results__list {
      @include grid-column(
        $columns: 6
      );
    }
  }

  .content-aside {
    position: relative;
    @include grid-row(
      $behavior: nest-collapse
    );
    padding: rem-calc(5) 0;

    &__tool,
    &__text {
      @include grid-column(
        $columns: 6
      );
      padding-left: 0;
    }

    &__text {
      font-size: rem-calc(18);
    }

    &__tool {
      @extend .btn-unstyled;
      padding-right: 0;
      display: table;
      > * {
        display: table-cell;
        vertical-align: middle;
      }
      &__text {
        font-size: rem-calc(18);
      }
      .icon-star {
        font-size: rem-calc(30);
        padding-right: rem-calc(10);
      }

      @media #{$medium-up} {
        @include position(absolute, null 0 rem-calc(-60) null);
        width: rem-calc(150);

        &__text {
          font-size: rem-calc(15);
        }

        .icon-star {
          font-size: rem-calc(22);
        }
      }
    }

  }


  &__list {
    margin-left: 0;
    .results__list__heading {
      border-bottom: 1px solid $input-border-color;
      margin-bottom: rem-calc(30);

      @media #{$medium-up} {
        border-bottom: none;
        margin-bottom: 0;
        .heading__title {
          font-size: rem-calc(40);
        }
      }
    }
  }


  &__tabs {
    text-align: center;
    font-size: 0;
    margin: 0;
    padding-bottom: rem-calc(30);

    @media #{$medium-up} {
      border-bottom: 1px solid $input-border-color;
      margin-bottom: rem-calc(30);
      text-align: left;
    }

    li {
      display: inline-block;

      &:nth-child(1n+2) {
        margin-left: rem-calc(10);

        @media #{$medium-up} {
          margin-left: rem-calc(20);
        }
      }
    }
  }


  .results-accordion {
    margin: 0;

    &__item {
      list-style: none;
      margin-bottom: rem-calc(10);
    }

    &__link {
      background-color: $lightGrey;
      display: block;
      padding: $button-med 7%;
      border: none;
      position: relative;

      .heading__title--sub {
        margin-bottom: 0;
      }

      @media #{$medium-up} {
        padding: ($button-lrg * 2) 7%;

        .heading__title {
          font-size: rem-calc(28);
          display: inline-block;

          &:first-child {
            &:after {
              content: ',';
              display: inline-block;
              margin-right: rem-calc(10);
            }
          }
        }
      }

      .tag {
        @include position(absolute, rem-calc(20) rem-calc(20) null null);
      }
    }
  }


  &__item {
    list-style: none;
    padding: rem-calc(25px) 0;

    @media #{$small-only} {
      &.bordered {
        border-bottom: 1px solid $mediumGrey;
      }
    }
  }
}