.searchFormular {
  padding-top: $form-spacing;
  padding-bottom: $form-spacing;

  &.searchFormular--results {
    background: $lightGrey;
  }

  &__more {
    @extend .btn-unstyled;
    @extend .show-for-small-only;
    @extend .dropdown__trigger;
    background: transparent;
    text-align: right;
    width: 100%;

    &[aria-expanded] {
      font-size: $paragraph-font-size;
      color: $grey;

      .icon-expand {
        color: $blue;
        margin-right: rem-calc(10);
      }
    }
  }

  .button-submit {
    @extend .btn-primary;
  }

  .input-withSubmit {
    position: relative;
    @include display(flex);

    .input {
      @include flex-grow(1);
    }

    .button-submit {
      width: rem-calc(60);
      height: $form-height;
      padding: $form-padding;
      border-radius: 0;
      .icon-search {
        margin-right: 0;
        position: static;
        @include transform(translateY(0));
      }
      @extend .btn-icon;
    }

    @media #{$medium-up} {
      display: block;
      .button-submit { display: none; }
    }
  }

  &__filters {
    @media #{$medium-up} {
      display: block;
    }
  }

  .extra-checkboxes {
    margin: $form-spacing 0;
  }

  button[type=submit] {
    @extend .btn-primary;
    @extend .btn-rounded;

    @media #{$small-only} {
      width: 100%;
    }
    margin-bottom: 0;
  }

  .with-columns {
    @media #{$small-only} {
      @include block-grid(2);
    }
    > li {
      padding-bottom: 0;
    }
  }

  .price-range {
    @include block-grid(
      $per-row: 2,
      $spacing: rem-calc(40)
    );

    > li {
      padding-bottom: 0;
      margin-bottom: 0;
      position: relative;
    }

    > li:first-child {
      &:after {
        content: "";
        @include position(absolute, 35% rem-calc(-10) null null);
        width: $form-spacing;
        height: 2px;
        background: $jet;
      }
    }
  }
}

@media #{$medium-up} {
  .searchFormular__form {
    position: relative;
    @include grid-row($behavior: nest-collapse);

    .input-withSubmit {
      @include grid-column(
        $columns: 4,
        $collapse: true
      );

      .input__search {
        padding-right: $form-padding;
        height: ($form-height * 1.5);
      }
    }

    .searchFormular__filters {
      @include grid-column(
        $columns: 8,
        $collapse: true
      );
    }

    .searchFormular__dropdowns {
      &:after {
        content: "";
        display: table;
        clear: both;
      }

      .form-dropdown {
        float: left;
        width: 20%;
        position: relative;
        border: none;

        &__trigger {
          border: $input-border-width $input-border-style $input-border-color;
          border-left: none;
          height: ($form-height * 1.5);
        }

        &:last-child {
          .form-dropdown__content {
            right: 0;
          }
        }
      }
    }

    .form-dropdown__bottom {
      .extra-checkboxes {
        @include position(absolute, null null null 0);
        width: 70%;
        margin: 0;

        > li {
          display: inline-block;
          vertical-align: middle;
          padding-right: ($form-spacing * 2);
        }
      }

      button[type=submit] {
        float: right;
        padding-left: rem-calc(40);
        padding-right: rem-calc(40);
        margin-bottom: 0;
      }
    }

  }

  // HOMEPAGE
  .searchFormular-homepage {
    button[type=submit] {
      height: ($form-height * 1.5);
      font-size: $input-font-size;
      border-radius: 0;
    }

    .input__search {
      border-top: none;
      border-left: none;
      border-bottom: none;
    }

    .searchFormular__dropdowns .form-dropdown__trigger {
      border-top: none;
      border-bottom: none;
    }
  }

}

@media #{$small-only} {
  body:not(.homepage) {
    .form-dropdown__content {
      position: relative;
      box-shadow: none;
      border: none;
      width: 100%;

      .bordered:after {
        display: none;
      }
    }
  }

  .searchFormular-homepage {
    .input__search {
      padding-right: $form-padding;
    }
    .searchFormular__dropdowns {
      @include grid-row();

      .form-dropdown {
        float: left;
        width: 48%;
        margin-left: 4%;
        .form-dropdown__content {
            right: 0;
          }
        &:first-child {
          margin-left: 0;
          .form-dropdown__content {
            left: 0;
          }
        }
      }
    }
  }
}



