// BACKGROUND COLORS
.bg-white       { background-color: $white; }
.bg-black       { background-color: $black; }
.bg-jet         { background-color: $jet; }
.bg-lightBlue   { background-color: $lightBlue; }
.bg-blue        { background-color: $blue; }
.bg-mediumBlue  { background-color: $mediumBlue; }
.bg-darkBlue    { background-color: $darkBlue; }
.bg-lightGrey   { background-color: $lightGrey; }
.bg-mediumGrey  { background-color: $mediumGrey; }
.bg-grey        { background-color: $grey; }
.bg-brown       { background-color: $brown; }
.bg-pink        { background-color: $pink; }


// VISIBILITY
.hidden {
  display: none;
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}


// UNSTYLE UL
.ul-unstyled {
  list-style: none;
  padding: 0;
  margin: 0;
  > li {
    padding: 0;
    margin: 0;
  }
}