@font-face {
    font-family: 'icons';
    font-weight: normal;
    font-style: normal;
    src: url('../assets/fonts/icons.eot');
    src: url('../assets/fonts/icons.woff') format('woff'),
         url('../assets/fonts/icons.ttf') format('truetype'),
         url('../assets/fonts/icons.eot?#iefix') format('embedded-opentype');
}

.icon[class^="icon-"],
.icon[class*=" icon-"] {
    display: inline-block;
    font-family: 'icons';
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    font-style: normal;
    font-variant: normal;
    font-weight: normal;
    line-height: 1;
    text-transform: none;
}

$icons: (
    search: '\EA01',
    close: '\EA02',
    expand: '\EA03',
    arrow: '\EA04',
    star: '\EA05'
);

@each $name, $icon in $icons {
    .icon-#{$name}:before {
        content: $icon;
        display: inline-block;
    }
}