@media #{$medium-only} {
  @media #{$portrait} {
    html, body {
      font-size: 80%
    }
  }
}


// WRAPPERS
.main-wrapper {
  width: 100%;
  margin: 0 auto;

  @media #{$medium-up} {
    max-width: $xlarge-breakpoint;
    min-width: em-calc(900);
    padding-left: 0;
    padding-right: 0;
  }
}

.wrapper {
  width: 100%;
  padding-left: 5%;
  padding-right: 5%;
  margin: 0 auto;

  @media #{$medium-up} {
    max-width: $large-breakpoint;
    padding-left: 1%;
    padding-right: 1%;
  }
}
