.text-bold {
  font-weight: $font-weight-bold;
}

.text-normal {
  font-weight: $font-weight-normal;
}

.heading {
  position: relative;
  padding: rem-calc(18) 0;

  &__title {
    font-weight: $font-weight-bold;
    line-height: 130%;
    font-size: rem-calc(24);
    margin: 0;
    color: $jet;
    display: block;
  }

  &__title--sub {
    color: $lightBlue;
    text-transform: capitalize;
    margin-bottom: rem-calc(10);
  }

  &__text {
    font-weight: $font-weight-bold;
    font-size: rem-calc(20);
  }

  @media #{$medium-up} {
    padding: rem-calc(40) 0 rem-calc(10);

    &__title {
      font-size: rem-calc(40);
    }
  }
}

.heading--bordered {
  &:before {
    content: "";
    @include position(absolute, 0 null null 0);
    width: rem-calc(40);
    height: rem-calc(5);
    background: $jet;
  }

  @media #{$medium-up} {
    padding: rem-calc(24) 0 rem-calc(20);

    &:before {
      content: "";
      @include position(absolute, 0 null null 0);
      width: rem-calc(70);
      height: rem-calc(8);
      background: $jet;
    }
  }
}

.heading--small {
  .heading__title {
    font-size: rem-calc(28*2/3);

    @media #{$medium-up} {
      font-size: rem-calc(26);
    }
  }
}

.heading--white {
  &:before {
    background: $white;
  }

  .heading__title {
    color: $white;
    line-height: 130%;
  }

  .heading__title--sub {
    color: rgba($white, .5);
  }

  .heading__title--sub strong {
    color: $white;
  }
}

.heading--light {
  &:before {
    background: $lightBlue;
  }

  .heading__title {
    font-weight: $font-weight-bold;
    line-height: 110%;
    color: $lightBlue;
  }

  .heading__title--sub {
    color: $blue;
  }

  .heading__text {
    font-size: rem-calc(30);
    font-weight: $font-weight-bold;
  }
}