.squaredImageItem {
  @include grid-row(
    $behavior: nest-collapse
  );

  &__img {
    @include grid-column(
      $columns: 4,
      $collapse: true
    );

    @media #{$medium-up} {
      @include grid-column(
        $columns: 5,
        $collapse: true
      );
    }

    img {
      width: 100%;
    }
  }

  &__infos {
    @include grid-column(
      $columns: 8
    );
    padding-left: ($column-gutter * 1.25);

    @media #{$medium-up} {
      @include grid-column(
        $columns: 7
      );
      padding-left: ($column-gutter * 2);
    }

    .heading {
      padding: 0;
      display: block;
    }
  }

  &__details {
    &__1,
    &__2 {
      margin: 0;
      line-height: rem-calc(30*2/3);
    }

    &__1 {
      margin-top: 7px;
      font-size: rem-calc(20*2/3);
      font-weight: $font-weight-bold;
      color: $jet;
    }
    &__2 {
      font-size: rem-calc(20*2/3);
      font-weight: $font-weight-normal;
      color: $grey;
      strong {
        font-weight: $font-weight-bold;
        font-size: rem-calc(30*2/3);
      }
    }

    @media #{$medium-up} {
      &__1,
      &__2 {
        font-size: rem-calc(20);
        line-height: rem-calc(30);
      }
    }
  }

  &__btn {
    margin-top: rem-calc(30);
    .btn-primary {
      margin-bottom: 0;
    }
  }

  &--stacked {
    .squaredImageItem {
      &__img {
        @include grid-column(
          $columns: 12,
          $collapse: true
        );
      }

      &__infos {
        @include grid-column(
          $columns: 12,
          $collapse: true
        );
      }
    }
  }

  &--stacked-small {
    @media #{$small-only} {
      .squaredImageItem {
        &__img {
          @include grid-column(
            $columns: 12,
            $collapse: true
          );
        }

        &__infos {
          @include grid-column(
            $columns: 12,
            $collapse: true
          );

          .heading {
            padding: rem-calc(30*2/3) 0;
            margin-bottom: rem-calc(30*2/3);
            border-bottom: 1px solid $mediumGrey;

            &__title {
              margin-bottom: 0;
              font-size: rem-calc(40*2/3);
            }
          }
        }

        &__details__1,
        &__details__2 {
          line-height: rem-calc(50*2/3);
          font-size: rem-calc(30*2/3);
          strong {
            font-size: rem-calc(40*2/3);
          }
        }

        &__btn {
          .btn-primary {
            width: 100%;
          }
        }

      }
    }
  }
}