.title-area {
  position: relative;

  .name {
    display: table;
    width: 230px;
    padding-right: 15px;
    border-right: 1px solid $grey;

    .title-area__link {
      display: table-cell;
      vertical-align: middle;

      img {
        vertical-align: initial;
      }
    }
  }

  .has-form {
    @include position(absolute, 0 -240px null null);
    min-width: 14rem;
  }
}



.sticky {
  &.fixed {
    box-shadow: 0 0 6px 0 rgba($black, .5);
  }
}