// INPUT (Foundation default styles are unconvenient)
input[type=search],
input[type=text],
input[type=number] {
  height: $form-height;
  padding: $form-padding;
  font-size: $input-font-size;
  font-family: $font-family-sans-serif;
  font-weight: $font-weight-medium;
  border: $input-border-width $input-border-style $input-border-color;

  @include placeholder {
    color: $grey;
    font-weight: $font-weight-normal;
  }

  &.small-padding {
    padding-left: rem-calc(5);
    padding-right: rem-calc(5);
  }
}


// SELECT-LIKE DROPDOWN
.form-dropdown {
  background: $input-bg-color;
  border: $input-border-width $input-border-style $input-border-color;
  border-radius: $input-border-radius;
  color: $input-font-color;
  margin-bottom: $form-spacing;
  min-height: $form-height;
  position: relative;

  &__trigger {
    @extend .btn-unstyled;
    position: relative;
    width: 100%;
    height: $form-height;
    padding: $form-padding rem-calc(60) $form-padding $form-padding;
    font-weight: $form-label-font-weight;
    font-size: $input-font-size;

    &:focus {
      background-color: $input-focus-bg-color;
      border-color: $input-focus-border-color;
    }

    .icon-expand {
      @include position(absolute, 50% 0 null null);
      @include transform(translateY(-50%));
      @include transition(.4s all ease);
      color: $blue;
      font-size: ($input-font-size * 1.35);
      margin-right: $form-padding;
    }

    &[aria-expanded="true"] {
      .icon-expand {
        @include transform(rotate(180deg) translateY(50%));
      }
    }
  }

  &__content {
    padding: $form-padding $form-padding 0 $form-padding;
    background: $input-bg-color;

    .undo-padding {
      margin-top: (-$form-padding);
    }

    position: absolute;
    z-index: 1;
    background: $input-bg-color;
    border: $input-border-width $input-border-style $input-border-color;
    width: rem-calc(250);
    box-shadow: rem-calc(3) rem-calc(3) rem-calc(3) 0 rgba($black, .2);

    .bordered {
      position: relative;

      &:after {
        content: "";
        //@include position(absolute, null (-$form-padding) 0 (-$form-padding));
        @include position(absolute, null 0 0 0);
        height: 1px;
        background-color: $input-border-color;
      }
    }

    .label-checkbox {
      display: block;
    }
  }
}


// CHECKBOX
.input-checkbox {
  @extend .visually-hidden;
  &:checked + label:before {
    background: $blue;
  }
  &:focus + label:before {
    box-shadow: 0 0 5px 0 $blue;
    outline: none;
  }
}

.label-checkbox {
  display: table;
  line-height: 1;
  padding: rem-calc(15) 0 ;
  @media #{$medium-up} {
    padding: rem-calc(15) 0;
  }
  cursor: pointer;

  &:before {
    content: "";
    width: rem-calc(30);
    height: rem-calc(30);
    border: 1px solid $blue;
    background: $white;
    display: table-cell;
    vertical-align: middle;

    @media #{$medium-up} {
      width: rem-calc(15);
      height: rem-calc(15);
    }
  }

  span {
    display: table-cell;
    vertical-align: middle;
    padding-left: $form-padding;
  }
}

input[type="checkbox"] + .label-checkbox {
  margin-right: 0;
  margin-left: 0;
}


// "?" picto
.infotip {
  position: relative;
  span { @extend .visually-hidden; }

  &:after {
    content: "?";
    cursor: pointer;
    color: $white;
    border-radius: 50%;
    background: $blue;
    width: rem-calc(20);
    height: rem-calc(20);
    @include position(absolute, 50% 0 null );
    @include transform(translateY(-50%));
    line-height: rem-calc(20);
    text-align: center;
    font-size: 80%;
  }
}

// TAG
.tag {
  display: inline-block;
  background-color: $pink;
  padding: $button-med;
  color: $white;
  text-transform: uppercase;
  font-size: rem-calc(14);
  font-weight: $font-weight-bold;
}