// BUTTONS
@mixin btn-style($color) {
  @include button(
    $bg: $color
  );
  padding: $button-med $button-lrg;

  &:hover {
    background-color: $white;
    color: $color;
    box-shadow: inset 0 0 0 1px $color;
  }

  @media #{$medium-up} {
    padding: $button-lrg ($button-lrg * 2);
  }

  .icon-arrow {
    margin-left: rem-calc(10);
    vertical-align: middle;
  }
}

.btn-primary {
  @include btn-style($blue);
}

.btn-secondary {
  @include btn-style($jet);
}


// FILTERS
.active {
  .btn-filter {
    border-color: $blue;
    color: $blue;
  }
}
.btn-filter {
  background: $white;
  font-size: $button-font-med;
  font-weight: $font-weight-bold;
  border: solid 3px $lightBlue;
  color: $lightBlue;
  padding: $button-sml ($button-lrg * 2);
  display: inline-block;
  transition-property: border-color, color;
  transition-duration: 300ms;
  transition-timing-function: ease-out;
  &:hover,
  &:focus {
    border-color: $blue;
    color: $blue;
  }

  @media #{$medium-up} {
    font-size: $button-font-lrg;
    padding-top: $button-med;
    padding-bottom: $button-med;
  }
}


// BUTTON WITH ICON
.btn-icon {
  position: relative;
  .button__content {
    display: inline-block;
    position: relative;
    padding-left: rem-calc(35);
  }

  .icon {
    vertical-align: middle;
    @include position(absolute, 50% null null 0);
    @include transform(translateY(-50%));
    font-size: 150%;
  }
}


// ROUNDED BUTTONS
.btn-rounded {
  border-radius: 4px;
  padding-top: ($button-med *2);
  padding-bottom: ($button-med *2);
  font-size: $button-font-sml;
}


// UNSTYLE BUTTON
.btn-unstyled {
  cursor: pointer;
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
  background: inherit;
  padding: inherit;
  text-align: inherit;
  border: none;
  display: inline-block;
  @include appearance(none);
  font-family: $font-family-sans-serif;

  &:focus,
  &:hover {
    background: inherit;
    color: inherit;
  }
}